.cart-dropdown {
	position: absolute;
	width: 24rem;
	height: 34rem;
	display: flex;
	flex-direction: column;
	padding: 0.5em;
	border: 1px solid black;
	background-color: white;
	top: var(--header-height);
	right: -2px;
	box-shadow: 3px 5px 8px var(--color-dark-2);
	border-top: navajowhite;

	.empty-message {
		font-size: 1.8rem;
		font-weight: 600;
		text-transform: capitalize;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.cart-items {
		height: 24rem;
		margin: 0 0 0.25em 0;
		display: flex;
		flex-direction: column;
		overflow: scroll;
	}

	button {
		margin-top: auto;
	}
}
